import {
  checkAuth
} from '../Auth/auth.js';

export default {
  nonComponent: true,
  install (Vue, options) {
    // dom
    const remove = (el) => el.parentNode.removeChild(el);

    Vue.directive('auth', {
      inserted: (el, binding, vNode) => {
        // valuestring or arraymodifiers
        let {
          value,
          modifiers
        } = binding;

        // 
        if (
          !(
            typeof value === 'string' ||
            value instanceof Array
          ) ||
          !value
        ) {
          remove(el);
          return console.error('stringarray.');
        }

        // string
        if (typeof value === 'string') {
          value = [value];
        }

        /**
         * 
         *  - every value
         *  - somevalue
         */
        if (
          (
            modifiers.every &&
            value.some(v => !checkAuth(v))
          ) ||
          (
            !modifiers.every &&
            value.every(v => !checkAuth(v))
          )
        ) {
          remove(el);
        }
      }
    });
  }
};
