import Directives from './packages/Directives';
import Filters from './packages/Filters';
import Layout from './packages/Layout';
import Sidebar from './packages/Sidebar';
import LayoutHeader from './packages/LayoutHeader';
import Navbar from './packages/Navbar';
import BasicData from './packages/BasicData';
import http from './packages/Http';
import auth from './packages/Auth';
import Utils from './packages/Utils';
import Space from './packages/Space';
import SpaceX from './packages/SpaceX';
import EventBus from './packages/EventBus';

import './theme/index.css';

//  
const components = [
  EventBus,
  Utils,
  Directives,
  Filters,
  // Layout,
  // Sidebar,
  // LayoutHeader,
  Space,
  SpaceX,
  http,
  // basicdata EventBus utils
  BasicData,
  auth,
  {
    nonComponent: true,
    install (Vue, options) {
      // element 
      Vue.prototype.$ELEMENT = {
        size: 'medium'
      };
    }
  }
];

//  install  Vue  use 
const install = function (Vue, options) {
  // 
  if (install.installed) return;

  // 
  const defaultOptions = {
    env: '',
    loginURL: 'loginurl',
    baseURL: 'baseApi',
    // 
    showTopNavs: true,
    // 
    showLeftMenus: true,
  };

  // 
  components.map(component => {
    if (component.nonComponent && component.install) {
      // 
      component.install(Vue, {
        ...defaultOptions,
        ...options
      });
    } else {
      Vue.component(component.name, component);
    }
  });
};

export default {
  //  install Vue.use() 
  install,
};

export {
  Layout,
  Sidebar,
  LayoutHeader,
  Navbar,
};
